@import url('https://unpkg.com/css.gg@2.0.0/icons/css/arrows-expand-right-alt.css');

.player-event-container {
  min-height: auto;
  border-radius: var(--border-md);
  display: flex;
  gap: var(--gap-md);
  flex-direction: column;
  justify-content: space-evenly;
  padding: var(--padding-md);
}

.win {
  background-image: url(https://media.discordapp.net/attachments/1137527038366978078/1145079281039446128/bg-bottom.png?width=916&height=295);
  background-color: var(--color-victory);
  background-repeat: no-repeat;
  background-position: center;
  background-blend-mode: darken;
  background-size: cover;
}

.loss {
  background-image: url(https://media.discordapp.net/attachments/1137527038366978078/1145079280594866196/bg-top.png?width=916&height=438);
  background-color: var(--color-loss);
  background-repeat: no-repeat;
  background-position: center;
  background-blend-mode: darken;
  background-size: cover;
}

.win.assistance {
  background-color: var(--color-assistance);
}

.event-gear-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: var(--gap-sm);
}

.title {
  font-size: var(--font-md);
  font-weight: 600;
}

.info-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.subtitle {
  font-size: var(--font-xl);
  font-weight: 700;
}

.time {
  font-size: var(--font-sm);
  font-weight: 400;
}

.gear-items {
  display: flex;
  gap: var(--gap-sm);
}

@media (min-width: 1280px) {
  .player-event-container {
    min-height: auto;
    border-radius: var(--border-md);
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    padding: var(--padding-md);
  }
}
